  animation-timing-function: cubic-bezier(0.63, 0.36, 0, 0.97);
  opacity: 1;
}
.anim-right.fade-out {
  animation-timing-function: cubic-bezier(0.63, 0.36, 0, 0.97);
}
.anim-right.fade-in {
  animation-name: anim-right-in;
}
.anim-right.fade-out {
  opacity: 0;
  animation-name: anim-right-out;
}
@keyframes anim-right-in {
  0% {
    opacity: 0;
    transform: translateX(-0.618rem);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes anim-right-out {
  0% {
    opacity: 1;
    transform: translateY(0);
  }
  100% {
    opacity: 0;
    transform: translateX(-0.618rem);
  }
}
.anim-top {
  animation-duration: 300ms;
  animation-timing-function: cubic-bezier(0.63, 0.36, 0, 0.97);
  opacity: 1;
}
.anim-top.fade-out {
  animation-timing-function: cubic-bezier(0.63, 0.36, 0, 0.97);
}
.anim-top.fade-in {
  animation-name: anim-top-in;
}
.anim-top.fade-out {
  opacity: 0;
  animation-name: anim-top-out;
}
@keyframes anim-top-in {
  0% {
    opacity: 0;
    transform: translateY(-0.618rem);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes anim-top-out {
  0% {
    opacity: 1;
    transform: translateY(0);
  }
  100% {
    opacity: 0;
    transform: translateY(-0.618rem);
  }
}
.anim-bottom {
  animation-duration: 300ms;
  animation-timing-function: cubic-bezier(0.63, 0.36, 0, 0.97);
  opacity: 1;
}
.anim-bottom.fade-out {
  animation-timing-function: cubic-bezier(0.63, 0.36, 0, 0.97);
}
.anim-bottom.fade-in {
  animation-name: anim-bottom-in;
}
.anim-bottom.fade-out {
  opacity: 0;
  animation-name: anim-bottom-out;
}
@keyframes anim-bottom-in {
  0% {
    opacity: 0;
    transform: translateY(0.618rem);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes anim-bottom-out {
  0% {
    opacity: 1;
    transform: translateY(0);
  }
  100% {
