.button__menu .icon-menu-custom {
  position: relative;
  vertical-align: top;
  width: 1em;
  height: .75em;
  top: .15em;
  font-size: 1.2rem;
}
.button__menu .icon-menu-custom span {
  display: block;
  position: absolute;
  height: 1px;
  width: 100%;
  background-color: rgba(255, 255, 255, 0.75);
  border-radius: 0;
  left: 0;
  transform: rotate(0deg);
  transition: all 1100ms cubic-bezier(0.19, 1, 0.22, 1);
}
.button__menu .icon-menu-custom span:nth-child(1) {
  top: 0;
  transform-origin: left center;
}
.button__menu .icon-menu-custom span:nth-child(2) {
  top: calc(.4em -  1px);
  transform-origin: left center;
}
.button__menu .icon-menu-custom span:nth-child(3) {
  top: calc(.8em -  2px);
  transform-origin: left center;
}
.button__menu.active .icon-menu-custom span:nth-child(1) {
  transform: rotate(45deg);
  top: 0;
  left: 0;
}
.button__menu.active .icon-menu-custom span:nth-child(2) {
  width: 0%;
  opacity: 0;
}
.button__menu.active .icon-menu-custom span:nth-child(3) {
  transform: rotate(-45deg);
  top: calc(.8em -  2px);
  left: 0;
}
.button__menu:hover .icon-menu-custom span,
.button__menu:active .icon-menu-custom span,
.button__menu.active .icon-menu-custom span {
  background-color: #ffffff;
}
