/* .make-line */
.make-line {
  position: relative;
}
.make-line .line-container {
  position: relative;
  width: 100%;
  overflow: hidden;
}
.make-line .line-container .line {
  display: inline-block;
  padding-right: 15px;
  background-color: #ffffff;
}
.make-line .line-container .line:after {
  content: "";
  position: absolute;
  z-index: -1;
  bottom: .5em;
  left: 0;
  width: 100%;
  height: 1px;
  background-color: #80d8ff;
}
h2 .make-line .line-container .line:after {
  height: 1px;
}
.make-line .line:after {
  left: 100%;
  width: 0;
  opacity: 0;
  transition: opacity 600ms cubic-bezier(0.19, 1, 0.22, 1) 100ms;
  animation: line-out 600ms cubic-bezier(0.19, 1, 0.22, 1);
}
.make-line:hover .line:after {
  left: 0;
  width: 100%;
  opacity: 1;
  transition: opacity 600ms cubic-bezier(0.19, 1, 0.22, 1) 100ms;
  animation: line-in 600ms cubic-bezier(0.19, 1, 0.22, 1);
}
@keyframes line-out {
  0% {
    left: 0;
    width: 100%;
  }
  100% {
    left: 100%;
    width: 0;
  }
}
@keyframes line-in {
  0% {
    left: 0;
    width: 0;
  }
  100% {
    left: 0;
    width: 100%;
  }
}
